@import url(https://fonts.googleapis.com/css?family=Nixie+One|Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*** Fonts ***/
/*** Colours ***/
body {
  background: #b6fbff;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #b6fbff, #83a4d4);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#b6fbff), to(#83a4d4));
  background: -webkit-linear-gradient(left, #b6fbff, #83a4d4);
  background: linear-gradient(to right, #b6fbff, #83a4d4);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: 'Open Sans', sans-serif; }

h1, h2 {
  font-family: 'Nixie One', serif;
  font-size: calc(10px + 2vmin); }

.App {
  text-align: center; }

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white; }

.sun {
  width: calc(80px + 5vmin);
  height: auto;
  align-self: center; }

.title {
  font-size: calc(10px + 6vmin); }

#city {
  border-radius: 4px;
  border: none;
  padding: .5em 1em;
  box-shadow: 0.2em 0.1em 0.3em #8b89897a;
  font-size: calc(10px + .5vmin);
  margin: 2em;
  -webkit-transition: all ease-in-out .3s;
  transition: all ease-in-out .3s; }
  #city:active, #city:focus {
    box-shadow: 0.2em 0.2em 0.8em #8b89897a; }

button {
  background-color: #83a4d4;
  border-radius: 4px;
  border: none;
  padding: .5em 1em;
  cursor: pointer;
  margin: 2em;
  box-shadow: 0.2em 0.1em 0.3em #8b89897a;
  font-size: calc(10px + .75vmin);
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s;
  color: #fff; }
  button:hover {
    background-color: #ccdcf3;
    box-shadow: 0.2em 0.2em 0.6em #68686891;
    color: #333; }
  button:active, button:focus {
    background-color: #aac2e6;
    box-shadow: 0.1em 0.1em 0.1em #68686891;
    color: #333; }

